<template>
  <div id="app">
    <div id="nav">
    </div>
    <router-view />
  </div>
</template>
<script>
export default {
  name:'App',
  provide() {
    return {
      reload: this.reload
    }
  },
  data() {
    return {
      isRouterAlive: true
    }
  },
  methods: {
    reload() {
      this.isRouterAlive = false
      this.$nextTick(function() {
        this.isRouterAlive = true
      })
    }
  }
};
</script>
