<template>
  <div id="login_box">
    <div class="login_div">
      <h2 id="title">管理员登录</h2>
      <el-input placeholder="请输入用户名" v-model="userMsg.name"></el-input><br><br>
      <el-input placeholder="请输入密码" type="password" v-model="userMsg.pwd"></el-input><br><br>
      <div class="login_btn_div">
        <el-button id="login_btn" round @click="submit">登录</el-button>
      </div>
    </div>
  </div>
</template>

<script>
// import axios from "axios";
import { req1 } from "@/network/request.js";
export default {
  name: "Login",
  data() {
    return {
      userMsg: {
        name: "",
        pwd: ""
      }
    };
  },
  methods: {
    submit() {
      console.log(this.userMsg);
      req1({
        url: '/login',
        methods: 'get',
        params: {
          username: this.userMsg.name,
          password: this.userMsg.pwd
        }
      }).then(res => {
        console.log(res);
        if(res == 'success'){
          localStorage.setItem('username',this.userMsg.name)
          this.$router.push('/home')
          // console.log(this.userMsg.name);
          
        }
      })
      
      
      
    }
  }
};
</script>

<style>
#login_box{
  height: 100%;
}
.login_div {
  width: 300px;
  padding: 50px 30px;
  background-color: rgba(255, 255, 255, 0.5);
  text-align: center;
  margin: 200px auto;
  border-radius: 15px;
}
.login_btn_div {
  margin: 10px auto;
  width: 300px;
}
#login_btn {
  width: 100%;
}
#title {
  font-size: 26px;
  font-weight: 900;
  margin-bottom: 30px;
}
</style>